.galerie {
   padding-left: 35px;
}

.nadpis {
   text-align: center;
   font-size: 40px;
}

.block {
   width: 350px;
   height: 350px;
   display: inline-block;
   padding: 20px;
   overflow: hidden;
}

.img {
   margin-left: auto;
   margin-right: auto;
   display: block;
   transition: 1s;
   padding: 15px;
   width: 100%;

   height: 100%;
}

.img:hover {
   transform: scale(1.3);
}

@media screen and (max-width: 960px) {
   .galerie {
      padding-left: 15px;
   }
   .nadpis {
      text-align: center;
      font-size: 30px;
   }
   .block {
      width: 150px;
      height: 150px;
      display: inline-block;
      padding: 10px;
   }
   .img {
      transition: 1s;
      padding: 0;
      width: 100%;
      height: 100%;
      object-fit: cover;
   }
}

@media screen and (max-width: 300px) {
   .nadpis {
      text-align: center;
      font-size: 30px;
   }
   .block {
      width: 130px;
      height: 130px;
   }
   .img {
      transition: 1s;
      padding: 0;
      width: 100%;
      height: 100%;
      object-fit: cover;
   }
}
