.nadpis {
   text-align: center;
   font-size: 40px;
}
.outer_wrapper {
   padding-bottom: 800px;
}
.produkty {
   width: 250px;
   height: 360px;
   border-radius: 10px 10px 10px 10px;
   box-shadow: 0;
   transform: scale(0.95);
   border: solid 2px black;
   transition: box-shadow 0.5s, transform 0.5s;
   float: right;

   /*    overflow-x: auto;
 */
}

.produkty:hover {
   transform: scale(1);
   box-shadow: 5px 20px 30px rgba(0, 0, 0, 0.2);
}

.details {
   text-align: center;
   line-height: 20px;
}
.jmeno {
   height: 60px;
   font-size: 25px;
}
.cena {
   font-size: 20px;

   line-height: 50px;
}
.icons {
   padding-bottom: 50px;
   padding: 0;
   margin: 0;
}
.buy {
   border-top: solid thin rgba(0, 0, 0, 0.1);

   width: 100%;
   height: 80px;

   font-size: 50px;
   border-radius: 0px 0px 10px 10px;

   text-align: center;
   cursor: pointer;
}

.buy:hover {
   transition: 0.5s;
   background: rgba(161, 244, 247, 1);
}

.images {
   width: 100%;
   height: 150px;
   content: cover;
   object-fit: scale-down;
}

.container {
   text-align: center;
}
.mnostvi {
   margin: 0;
   padding: 0;
}
@media screen and (max-width: 960px) {
   .outer_wrapper {
      display: inline;
   }
   .produkty {
      background: white;
      margin: 0 auto;
      border-radius: 10px 10px 10px 10px;
      box-shadow: 0;
      transform: scale(0.95);
      float: none;
      border: solid 2px black;
      transition: box-shadow 0.5s, transform 0.5s;
   }
}

@media screen and (max-width: 280px) {
   .outer_wrapper {
      display: inline;
   }
   .produkt {
      width: 80%;
      height: 80%;
      background: white;
      margin: 0 auto;
      border-radius: 10px 10px 10px 10px;
      box-shadow: 0;
      transform: scale(0.95);
      float: none;
      border: solid 2px black;
      transition: box-shadow 0.5s, transform 0.5s;
   }
   .images {
      width: 80%;
      height: 80%;
      object-position: center;
   }
}
