.card {
   display: flex;
}

.dph {
   font-size: 10px;
}
.img {
   width: 200px;
   padding-top: 20px;
   margin-right: 50px;
   height: 100%;
}
.name {
   margin-top: 20px;
}
.price {
   margin-left: 80px;
   padding-top: 50px;
}
.rightSide {
}
.buy {
   display: flex;
   margin-left: 350px;
}
.btn {
   width: 150px;
   height: 50px;
   margin-bottom: 50px;
   background-color: orange;
   margin-left: 100px;
   color: white;
   border-radius: 5px 5px 5px 5px;
}
.quantity {
   font-size: 25px;
   background-color: white;
   width: 50px;
   text-align: center;
}

.quantity::-webkit-outer-spin-button,
.quantity::-webkit-inner-spin-button {
   -webkit-appearance: none;
   margin: 0;
}
.minus,
.quantity,
.plus {
   border: none;
   color: black;
   text-decoration: none;
   font-size: 25px;
   background: wheat;
   height: 50px;
}
.arrow {
   position: absolute;
   padding-top: 20px;
   color: black;
   font-size: 35px;
   left: 250px;
}
.arrow:hover {
   color: #6568f4;
}
