.DPH {
   display: block;
   text-align: right;
   font-size: 20px;
}

.btn {
   padding: 10px;
   margin: auto;
   display: block;
   font-size: 30px;
   border-radius: 10px;
   background: greenyellow;
}

.btn:active {
   transform: translateY(4px);
}

.nadpis {
   text-align: center;
   font-size: 40px;
}
.cell {
}
.celek {
   position: relative;
   margin: 10px;
}

.blok {
   position: flex;
   width: 600px;
   height: 100px;
   background-color: #fbfbfb;
   border: solid 1px;
   border-radius: 5px;
   display: flex;
   margin: 0 auto;
   /*    justify-content: center;
   align-items: center; */
}

.img {
   width: 10%;
   height: 100%;
   object-fit: scale-down;
   padding-right: 0px;
}
.name {
   margin: auto 0;
   font-size: 20px;
}

.buttons {
   display: flex;
   margin: auto 30px;
   padding-left: 70px;
}
.total {
   margin: auto 0px;
   position: relative;
   padding-left: 150px;
}
.minus,
.plus {
   border: none;
   color: black;
   text-decoration: none;
   font-size: 20px;
   margin: auto 10px;
}

.quantity {
   margin: auto 0;
   font-size: 25px;
}

.bin {
   color: black;
   position: relative;
   font-size: 20px;
   text-decoration: none;
   margin: auto 0px;
}

.empty {
   font-size: 40px;
   text-align: center;
}

@media screen and (max-width: 960px) {
   .blok {
      padding-top: 0px;
      width: 100%;
      height: 60px;
      padding: 0px;
      margin: 0px;
   }
   .img {
      width: 10%;
      height: 60px;
      object-fit: scale-down;
   }

   .buttons,
   .total {
      font-size: 10px;
      position: relative;
      display: flex;
      padding: 0px;
      margin: auto 0px;
      position: relative;
      left: 0;
   }

   .name {
      display: flex;
      font-size: 15px;
   }
   .bin {
   }
}
