html,
body {
   height: 100%;
   margin: 0;
   background: url('./components/Galerie/fotky/08_krepelka.jpg') left/auto
         no-repeat,
      url('./components/Galerie/fotky/08_krepelkaRight.jpg') right no-repeat;

   background-size: 400px;
   /*    background-color: rgba(0, 0, 0, 0.03); */
}

* {
   margin: 0;
   padding: 0;
}

.content {
   min-height: calc(100vh - 35px);
   padding: 0 0 0 0;
}

.footer {
   height: 144px;
}
@media screen and (max-width: 960px) {
   html,
   body {
      background: none;
   }
}
